*{margin:0;padding:0;box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

*{margin:0;padding:0;box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.app{width:100%;height:100vh}

.header{background-color:#116466;height:100vh;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.header .title{font-size:22px;font-family:'Raleway', sans-serif;font-size:40px;font-weight:500;text-transform:uppercase;text-align:center;width:100%;line-height:1.5}.header .title__accent{font-family:'Pacifico', cursive;font-size:60px;color:#ffcb9a;padding:30px 0}.header .start{display:flex;flex-direction:column;align-items:center}.header .start p{font-size:22px;font-family:'Raleway', sans-serif;color:#ffcb9a;padding:200px 0 20px 0}.header .start .arrow{color:#d1e8e2;width:75px;height:75px;cursor:pointer}.header .start .arrow:hover{transform:scale(1.15)}

.choice{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:5% 0;width:100%;background-color:white}.choice__input-wrapper{width:750px;margin:10px 0 30px 0}.choice__city-input{padding:10px;margin:10px 0;outline:none;font-size:22px;font-family:'Raleway', sans-serif;font-family:'Open Sans Condensed', sans-serif;position:relative;margin:0;width:100%;border-radius:5px;border:1px solid gray}.choice__autocomplete{position:absolute;z-index:5;background-color:#d1e8e2;width:750px}.choice__autocomplete .location{cursor:pointer;font-family:'Open Sans Condensed', sans-serif;font-size:20px;padding:5px 10px;border-bottom:0.5px solid #969696}.choice__autocomplete .location:last-child{border:none}.choice__autocomplete .location:hover{background-color:#ffcb9a}.choice__confirm{padding:10px;margin:10px 0;outline:none;font-size:22px;font-family:'Raleway', sans-serif;font-weight:500;cursor:pointer;padding:15px 50px;background-color:#116466;color:white;border:none;border-radius:10px}.choice p{font-size:22px;font-family:'Raleway', sans-serif}.error{font-size:22px;font-family:'Raleway', sans-serif;color:#c42419;padding-top:20px}.input-error{border-color:#c42419}

.search-container{min-height:100vh;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.search-container__inside{background-color:#d1e8e2;padding:50px 10%;width:100%;display:flex;align-items:center}.chosen{width:100%;height:15vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.chosen__describ{width:100%;display:flex;align-items:center;justify-content:space-between;line-height:1.5;font-size:22px;font-family:'Raleway', sans-serif}.chosen__describ p{font-size:22px;font-family:'Raleway', sans-serif;text-align:end;text-align:top;width:10%}.chosen__describ div{width:90%;padding:10px 20px;text-align:start}.confirm{font-size:22px;font-family:'Raleway', sans-serif;font-weight:500;outline:none;cursor:pointer;padding:15px 25px;background-color:#116466;color:white;border:none;border-radius:10px}.globe-container{width:100%;display:flex;align-items:center;justify-content:space-between;padding:0 10%}.globe-container .earth-container{margin:0 auto}.globe-container .earth-container canvas{outline:none}@media (max-width: 1200px){.globe-container{flex-direction:column}}@media (max-width: 992px){.search-container__inside{flex-direction:column}.globe-container{padding:0}}

.earth-container{position:relative}.loader-container{position:absolute;top:0;width:100%;height:100%;background-color:white;display:flex;justify-content:center;align-items:center}.loading-spinner{-webkit-animation:rotation 1s infinite linear;animation:rotation 1s infinite linear}@-webkit-keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(359deg)}}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(359deg)}}

.weather{font-size:22px;font-family:'Raleway', sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;color:white;background-color:#116466;height:500px;padding:0 80px;border-radius:30px}.weather h1{color:#ffcb9a;font-size:28px;font-weight:600;padding-bottom:50px}.weather__icon{text-align:center}.weather p{padding:10px}@media (max-width: 1200px){.weather{margin-bottom:50px}}

